.row__title {
  padding-top: 20px;
  padding-bottom: 5px;
}
.row {
  color: #fff;
  margin-left: 20px;
}

.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
}
.row__posters::-webkit-scrollbar {
  display: none;
}
.row__poster {
  object-fit: contain;
  width: 100%;
  max-height: 100px;
  margin-right: 10px;
  transition: transform 450ms;
}
.row__poster:hover {
  cursor: pointer;
  transform: scale(1.08);
}
.row__poster__large {
  max-height: 250px;
}
.row__poster__large:hover {
  cursor: pointer;
  transform: scale(1.09);
}